import React, {Component} from 'react';
import axios from 'axios';

import {Thing} from './thing';

const styles = {
  container: {
    margin: '1rem'
  },
  h2: {
    fontWeight: 300,
    fontSize: '1.5rem'
  },
  things: {
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'space-around'
  }
};

export class Things extends Component {
  constructor() {
    super();
    this.state = {things: []};
  }

  componentDidMount() {
    axios
      .get('app/things/things.json')
      .then(response => {
        this.setState({things: response.data});
      });
  }

  render() {
    return (
      <div style={styles.container}>
        <h2 style={styles.h2}>
          Here are some things that Ross made:
        </h2>
        <div style={styles.things}>
          {this.state.things.map((thing, i) => (
            <Thing key={i} thing={thing}/>
          ))}
        </div>
      </div>
    );
  }
}



